"use client"

import { Section } from "./ui/section"
import { useRef, useEffect } from "react"
import { Droplets, Zap, Scale, Sparkles, Trash2, Filter, Brush, Waves, Search } from "lucide-react"
import gsap from "gsap"
import { ScrollTrigger } from "gsap/ScrollTrigger"

if (typeof window !== "undefined") {
  gsap.registerPlugin(ScrollTrigger)
}

const services = [
  { icon: Droplets, title: "Water Testing", description: "Precise chemical analysis using advanced digital photometers." },
  { icon: Zap, title: "Pool Shocking", description: "Eliminate bacteria and algae with premium shock treatments." },
  { icon: Scale, title: "Water Balance", description: "Perfect pH and alkalinity alignment for swimmer comfort." },
  { icon: Sparkles, title: "Sanitize", description: "Complete sanitization ensuring a safe swimming environment." },
  { icon: Trash2, title: "Empty Baskets", description: "Thorough cleaning of skimmer and pump baskets." },
  { icon: Filter, title: "Filter Cleaning", description: "Deep cleaning to maintain optimal filtration efficiency." },
  { icon: Brush, title: "Brush Pool", description: "Scrubbing walls and tiles to prevent algae buildup." },
  { icon: Waves, title: "Skim Surface", description: "Removal of all floating debris for a glass-like finish." },
  { icon: Search, title: "Vacuum Pool", description: "Precision vacuuming to remove sediment and debris." },
]

export function Services() {
  const sectionRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!sectionRef.current) return

    const ctx = gsap.context(() => {
      gsap.from(".services-title", {
        y: 80,
        opacity: 0,
        scale: 0.9,
        duration: 1.2,
        ease: "back.out(1.4)",
        scrollTrigger: { trigger: ".services-title", start: "top 80%" },
      })

      gsap.from(".service-card", {
        y: 100,
        opacity: 0,
        rotationX: -30,
        stagger: 0.08,
        duration: 1,
        ease: "power3.out",
        scrollTrigger: { trigger: ".services-grid", start: "top 75%" },
      })

      // 3D hover effect
      document.querySelectorAll(".service-card").forEach((card) => {
        card.addEventListener("mouseenter", function () {
          gsap.to(card, { y: -15, scale: 1.05, rotationY: 5, duration: 0.4, ease: "power2.out" })
        })
        card.addEventListener("mouseleave", function () {
          gsap.to(card, { y: 0, scale: 1, rotationY: 0, duration: 0.4, ease: "power2.out" })
        })
      })
    }, sectionRef)

    return () => ctx.revert()
  }, [])

  return (
    <Section id="services" className="bg-white relative overflow-hidden">
      {/* Background Pattern */}
      <div className="absolute inset-0 opacity-[0.03]">
        <div className="absolute inset-0" style={{ backgroundImage: `url("data:image/svg+xml,%3Csvg width='80' height='80' viewBox='0 0 80 80' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%2314b8a6' fill-opacity='1'%3E%3Cpath d='M50 50c0-5.523 4.477-10 10-10s10 4.477 10 10-4.477 10-10 10c0 5.523-4.477 10-10 10s-10-4.477-10-10 4.477-10 10-10zM10 10c0-5.523 4.477-10 10-10s10 4.477 10 10-4.477 10-10 10c0 5.523-4.477 10-10 10S0 25.523 0 20s4.477-10 10-10zm10 8c4.418 0 8-3.582 8-8s-3.582-8-8-8-8 3.582-8 8 3.582 8 8 8zm40 40c4.418 0 8-3.582 8-8s-3.582-8-8-8-8 3.582-8 8 3.582 8 8 8z' /%3E%3C/g%3E%3C/g%3E%3C/svg%3E")` }} />
      </div>

      <div ref={sectionRef} className="container mx-auto px-6 relative z-10">
        <div className="services-title text-center max-w-4xl mx-auto mb-20">
          <div className="inline-flex items-center gap-2 px-6 py-3 rounded-full bg-teal-500/10 border border-teal-500/20 mb-8">
            <Sparkles className="w-5 h-5 text-teal-600" />
            <span className="text-sm font-bold tracking-wider uppercase text-teal-700">Our Services</span>
          </div>

          <h2 className="font-['Playfair_Display'] text-5xl md:text-6xl lg:text-7xl font-bold mb-6 text-slate-900">
            <span className="bg-gradient-to-r from-teal-600 via-cyan-600 to-teal-600 bg-clip-text text-transparent">
              Premium Care Packages
            </span>
          </h2>
          <p className="text-xl text-slate-600 leading-relaxed font-['Montserrat']">
            Comprehensive solutions tailored to your pool's unique needs, delivered with precision and excellence.
          </p>
        </div>

        <div className="services-grid grid md:grid-cols-2 lg:grid-cols-3 gap-8">
          {services.map((service, index) => (
            <div
              key={index}
              className="service-card group bg-white p-10 rounded-3xl shadow-lg hover:shadow-2xl transition-all duration-500 border border-slate-200/50 relative overflow-hidden"
              style={{ perspective: "1000px" }}
            >
              <div className="absolute inset-0 bg-gradient-to-br from-teal-500/5 to-cyan-500/5 opacity-0 group-hover:opacity-100 transition-opacity duration-500" />
              
              <div className="relative mb-8">
                <div className="w-20 h-20 bg-gradient-to-br from-teal-500 to-cyan-500 rounded-2xl flex items-center justify-center shadow-xl group-hover:shadow-2xl group-hover:shadow-teal-500/40 group-hover:scale-110 group-hover:rotate-6 transition-all duration-500">
                  <service.icon className="w-10 h-10 text-white" />
                </div>
                <div className="absolute inset-0 bg-gradient-to-br from-teal-500 to-cyan-500 rounded-2xl blur-2xl opacity-0 group-hover:opacity-30 transition-opacity duration-500" />
              </div>

              <h3 className="text-2xl font-bold text-slate-900 mb-4 group-hover:text-teal-600 transition-colors duration-300 font-['Playfair_Display']">
                {service.title}
              </h3>
              <p className="text-slate-600 leading-relaxed font-['Montserrat']">{service.description}</p>

              <div className="absolute top-0 right-0 w-24 h-24 bg-gradient-to-br from-transparent to-teal-500/10 rounded-bl-full opacity-0 group-hover:opacity-100 transition-opacity duration-500" />
            </div>
          ))}
        </div>

        <div className="text-center mt-20">
          <a
            href="#contact"
            className="inline-flex items-center gap-3 px-12 py-6 bg-gradient-to-r from-teal-600 to-cyan-500 text-white text-xl font-bold rounded-full hover:shadow-2xl hover:shadow-teal-500/60 hover:scale-110 transition-all duration-500"
          >
            Get Your Custom Quote
            <Sparkles className="w-6 h-6" />
          </a>
        </div>
      </div>
    </Section>
  )
}
