"use client"

import { Section } from "./ui/section"
import { useRef, useEffect } from "react"
import { Play, Sparkles } from "lucide-react"
import gsap from "gsap"
import { ScrollTrigger } from "gsap/ScrollTrigger"

if (typeof window !== "undefined") {
  gsap.registerPlugin(ScrollTrigger)
}

const videos = [
  "https://videos.pexels.com/video-files/3205916/3205916-hd_1920_1080_25fps.mp4",
  "https://videos.pexels.com/video-files/4325457/4325457-hd_1920_1080_30fps.mp4",
  "https://videos.pexels.com/video-files/6110556/6110556-hd_1920_1080_25fps.mp4",
  "https://videos.pexels.com/video-files/3205916/3205916-hd_1920_1080_25fps.mp4",
  "https://videos.pexels.com/video-files/4325457/4325457-hd_1920_1080_30fps.mp4",
  "https://videos.pexels.com/video-files/6110556/6110556-hd_1920_1080_25fps.mp4",
]

export function Gallery() {
  const sectionRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!sectionRef.current) return

    const ctx = gsap.context(() => {
      gsap.from(".gallery-title", {
        scale: 0.8,
        opacity: 0,
        duration: 1.2,
        ease: "back.out(1.5)",
        scrollTrigger: { trigger: ".gallery-title", start: "top 80%" },
      })

      gsap.from(".gallery-video", {
        scale: 0.7,
        opacity: 0,
        rotation: -10,
        stagger: 0.12,
        duration: 1,
        ease: "back.out(1.5)",
        scrollTrigger: { trigger: ".gallery-grid", start: "top 75%" },
      })
    }, sectionRef)

    return () => ctx.revert()
  }, [])

  return (
    <Section id="gallery" className="bg-gradient-to-br from-slate-950 via-teal-950 to-slate-900 text-white relative overflow-hidden">
      <div className="absolute inset-0 opacity-20">
        <div className="absolute top-1/4 left-1/4 w-[600px] h-[600px] bg-teal-500 rounded-full blur-[150px] animate-pulse" />
        <div className="absolute bottom-1/4 right-1/4 w-[500px] h-[500px] bg-cyan-500 rounded-full blur-[140px] animate-pulse" />
      </div>

      <div ref={sectionRef} className="container mx-auto px-6 relative z-10">
        <div className="gallery-title text-center mb-20">
          <div className="inline-flex items-center gap-2 px-6 py-3 rounded-full bg-teal-500/10 backdrop-blur-xl border border-teal-500/20 mb-8">
            <Sparkles className="w-5 h-5 text-teal-400" />
            <span className="text-sm font-bold tracking-wider uppercase text-teal-300">Portfolio</span>
          </div>

          <h2 className="font-['Playfair_Display'] text-5xl md:text-6xl lg:text-7xl font-bold mb-6">
            <span className="bg-gradient-to-r from-teal-200 via-cyan-300 to-teal-200 bg-clip-text text-transparent">
              Our Work in Motion
            </span>
          </h2>
          <p className="text-xl text-teal-100/80 max-w-3xl mx-auto leading-relaxed font-['Montserrat']">
            Witness the transformation and meticulous care we bring to every pool. Excellence in action.
          </p>
        </div>

        <div className="gallery-grid grid md:grid-cols-2 lg:grid-cols-3 gap-8">
          {videos.map((src, index) => (
            <div
              key={index}
              className="gallery-video relative aspect-video rounded-3xl overflow-hidden group cursor-pointer shadow-2xl hover:shadow-teal-500/50 transition-all duration-500"
            >
              <video src={src} muted loop playsInline className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-110" onMouseOver={(e) => e.currentTarget.play()} onMouseOut={(e) => e.currentTarget.pause()} />
              
              <div className="absolute inset-0 bg-gradient-to-t from-teal-950/80 via-transparent to-transparent opacity-60 group-hover:opacity-0 transition-opacity duration-500" />
              
              <div className="absolute inset-0 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity duration-300">
                <div className="w-20 h-20 bg-gradient-to-br from-teal-500 to-cyan-500 backdrop-blur-md rounded-full flex items-center justify-center shadow-2xl shadow-teal-500/60 group-hover:scale-125 transition-transform duration-300">
                  <Play className="w-9 h-9 text-white fill-white ml-1" />
                </div>
              </div>

              <div className="absolute inset-0 rounded-3xl ring-2 ring-teal-500/0 group-hover:ring-teal-500/50 transition-all duration-500" />
            </div>
          ))}
        </div>
      </div>
    </Section>
  )
}
