"use client"

import { Section } from "./ui/section"
import { useRef, useEffect } from "react"
import { Send, Phone, Mail, Clock, MapPin, Sparkles } from "lucide-react"
import gsap from "gsap"
import { ScrollTrigger } from "gsap/ScrollTrigger"

if (typeof window !== "undefined") {
  gsap.registerPlugin(ScrollTrigger)
}

export function Contact() {
  const sectionRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!sectionRef.current) return

    const ctx = gsap.context(() => {
      gsap.from(".contact-info", {
        x: -80,
        opacity: 0,
        duration: 1,
        ease: "power3.out",
        scrollTrigger: { trigger: ".contact-info", start: "top 80%" },
      })

      gsap.from(".contact-form", {
        x: 80,
        opacity: 0,
        duration: 1,
        ease: "power3.out",
        scrollTrigger: { trigger: ".contact-form", start: "top 80%" },
      })

      gsap.from(".contact-item", {
        y: 30,
        opacity: 0,
        stagger: 0.15,
        duration: 0.8,
        ease: "power2.out",
        scrollTrigger: { trigger: ".contact-items", start: "top 85%" },
      })
    }, sectionRef)

    return () => ctx.revert()
  }, [])

  return (
    <Section id="contact" className="bg-gradient-to-br from-teal-900 via-cyan-900 to-slate-900 text-white relative overflow-hidden">
      <div className="absolute inset-0 opacity-20">
        <div className="absolute top-0 left-0 w-[600px] h-[600px] bg-teal-500 rounded-full blur-[150px]" />
        <div className="absolute bottom-0 right-0 w-[600px] h-[600px] bg-cyan-500 rounded-full blur-[150px]" />
      </div>

      <div ref={sectionRef} className="container mx-auto px-6 relative z-10">
        <div className="grid lg:grid-cols-2 gap-16">
          <div className="contact-info">
            <div className="inline-flex items-center gap-2 px-6 py-3 rounded-full bg-teal-500/20 backdrop-blur-sm border border-teal-500/30 mb-8">
              <Sparkles className="w-5 h-5 text-teal-400" />
              <span className="text-sm font-bold tracking-wider uppercase text-teal-300">Get In Touch</span>
            </div>

            <h2 className="font-['Playfair_Display'] text-5xl md:text-6xl lg:text-7xl font-bold mb-6 leading-tight">
              <span className="bg-gradient-to-r from-teal-200 via-cyan-200 to-teal-200 bg-clip-text text-transparent">
                Let's Create Your Paradise
              </span>
            </h2>

            <p className="text-teal-100/80 text-xl mb-12 leading-relaxed font-['Montserrat']">
              Ready to transform your pool into a stunning oasis? Our team of experts is standing by to provide you with a complimentary consultation and quote.
            </p>

            <div className="contact-items space-y-8">
              {[
                { icon: Phone, label: "Call Us", value: "+1 (555) 123-4567" },
                { icon: Mail, label: "Email Us", value: "hello@dogepools.com" },
                { icon: Clock, label: "Working Hours", value: "Mon - Sat: 8am - 6pm" },
                { icon: MapPin, label: "Service Area", value: "Greater Metro Area" },
              ].map((item, i) => (
                <div key={i} className="contact-item group flex items-center gap-5">
                  <div className="w-16 h-16 bg-gradient-to-br from-teal-500 to-cyan-500 rounded-2xl flex items-center justify-center group-hover:scale-110 group-hover:rotate-6 transition-all duration-500 shadow-xl">
                    <item.icon className="w-8 h-8 text-white" />
                  </div>
                  <div>
                    <p className="text-sm text-teal-300 uppercase tracking-wider font-bold mb-1">{item.label}</p>
                    <p className="text-xl font-semibold text-white group-hover:text-teal-200 transition-colors">{item.value}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>

          <div className="contact-form">
            <div className="bg-white/5 backdrop-blur-2xl p-8 md:p-10 rounded-3xl shadow-2xl border border-white/10">
              <form className="space-y-6">
                <div className="grid md:grid-cols-2 gap-6">
                  {["First Name", "Last Name"].map((label) => (
                    <div key={label} className="space-y-2">
                      <label className="text-sm font-bold text-teal-200 uppercase tracking-wider">{label}</label>
                      <input type="text" className="w-full px-5 py-4 rounded-xl bg-white/5 backdrop-blur-sm border border-teal-500/20 focus:border-teal-400 focus:ring-2 focus:ring-teal-400/30 outline-none transition-all text-white placeholder-white/30" placeholder={label === "First Name" ? "John" : "Doe"} />
                    </div>
                  ))}
                </div>

                <div className="space-y-2">
                  <label className="text-sm font-bold text-teal-200 uppercase tracking-wider">Email Address</label>
                  <input type="email" className="w-full px-5 py-4 rounded-xl bg-white/5 backdrop-blur-sm border border-teal-500/20 focus:border-teal-400 focus:ring-2 focus:ring-teal-400/30 outline-none transition-all text-white placeholder-white/30" placeholder="john@example.com" />
                </div>

                <div className="space-y-2">
                  <label className="text-sm font-bold text-teal-200 uppercase tracking-wider">Phone Number</label>
                  <input type="tel" className="w-full px-5 py-4 rounded-xl bg-white/5 backdrop-blur-sm border border-teal-500/20 focus:border-teal-400 focus:ring-2 focus:ring-teal-400/30 outline-none transition-all text-white placeholder-white/30" placeholder="+1 (555) 000-0000" />
                </div>

                <div className="space-y-2">
                  <label className="text-sm font-bold text-teal-200 uppercase tracking-wider">Service Type</label>
                  <select className="w-full px-5 py-4 rounded-xl bg-white/5 backdrop-blur-sm border border-teal-500/20 focus:border-teal-400 focus:ring-2 focus:ring-teal-400/30 outline-none transition-all text-white">
                    <option className="bg-slate-900">Weekly Maintenance</option>
                    <option className="bg-slate-900">One-time Cleaning</option>
                    <option className="bg-slate-900">Pool Repair</option>
                    <option className="bg-slate-900">Construction</option>
                  </select>
                </div>

                <div className="space-y-2">
                  <label className="text-sm font-bold text-teal-200 uppercase tracking-wider">Message</label>
                  <textarea rows={4} className="w-full px-5 py-4 rounded-xl bg-white/5 backdrop-blur-sm border border-teal-500/20 focus:border-teal-400 focus:ring-2 focus:ring-teal-400/30 outline-none transition-all text-white placeholder-white/30 resize-none" placeholder="Tell us about your pool..." />
                </div>

                <button type="submit" className="w-full bg-gradient-to-r from-teal-500 to-cyan-500 text-white font-bold py-5 rounded-xl hover:shadow-2xl hover:shadow-teal-500/60 transition-all transform hover:scale-[1.02] flex items-center justify-center gap-3 text-lg group">
                  Send Your Request
                  <Send className="w-5 h-5 group-hover:translate-x-1 transition-transform" />
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </Section>
  )
}
